import { mapValues } from 'lodash';
import type { OptionsStanza } from './AST';
import { ColumnarData, DataFrame } from './DataFrame';
import { DataFrameLite } from './DataFrameLite';
import EncodingExecutor from './EncodingExecutor';
import type { Frames } from './EncodingExecutor';

export class Options {
    /**
     * Takes the options stanza and the dataSources, and returns a simple properties object. Any options
     * that were expressions are evaluated. The resulting object is suitable to pass to a pure viz as props.
     * @param {OptionsStanza} options
     * @param dataSources
     * @param themeFunc
     * @returns {object}
     */
    static evaluate(
        options: OptionsStanza,
        dataSources: { [key: string]: ColumnarData },
        themeFunc?: (themeKey) => any,
        dataFramesIn?: Frames,
        bypassCloneDeepOptionScope?: boolean,
        lazyLoadingDataFrame?: boolean
    ) {
        let dataFrames: Frames;
        if (dataFramesIn) {
            dataFrames = dataFramesIn;
        } else {
            dataFrames = Options.dataFrameFromDataSource(dataSources, lazyLoadingDataFrame);
        }
        return new EncodingExecutor().executeOptions(
            options,
            dataFrames,
            themeFunc,
            bypassCloneDeepOptionScope
        );
    }

    static dataFrameFromDataSource(
        dataSources: { [key: string]: ColumnarData },
        lazyLoadingDataFrame?: boolean
    ): Frames {
        if (lazyLoadingDataFrame) {
            return mapValues(dataSources, ds => DataFrameLite.fromJsonCols(ds));
        }
        return mapValues(dataSources, ds => DataFrame.fromJsonCols(ds));
    }
}
